.tags {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  margin-bottom: 2em;
  flex-wrap: wrap;
}

.tag {
  display: block;
  margin-right: 1rem;
  margin-top: 2.5rem;
  cursor: pointer;
  &__choice {
    display: flex;
    justify-content: center;
    align-items: center;
    input {
      width: auto;
      right: -1.5rem;
      margin-right: -1rem;
      position: relative;
      z-index: 2;
      & + label {
        border-radius: 5px;
        background: $grey;
        padding: 1rem 1rem 1rem 4rem;
        cursor: pointer;
      }
      &:checked + label {
        background: $color-teal;
        color: $color-white;
      }
    }
  }
  &__link {
    border: 1px solid $color-lightgray;
    background:white;
    border-radius: 5px;
    color:$color-black;
    font-weight: 400;
    font-family: $raleway;
    display: flex;
    &--active {
      background: $color-teal;
      .tag__text {
        color: white;
      }
    }
  }
  &__text {
    padding: 1rem 1rem;
    display: block;
  }
  &__count {
    padding: 1rem 1rem;
    background: white;
    border-left: 3px solid rgba(0,0,0,0.1);
    margin-right: -1px; /* ugh */
    background-clip: padding-box;
  }
}

.mgnone {
  // margin-top: 1em;
  margin-bottom: 0;
}



